<template>
  <v-container>
    <v-card class="mt-10 pa-4" elevation="12">
      <v-card-title
        >

        <v-btn text @click="Onclick_battleEndBtn" v-if="combat"></v-btn>
        <v-btn text @click="Onclick_battleStartBtn" v-else> </v-btn>
        <!-- <v-btn text @click="test"> </v-btn> -->
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="lists"
        :items-per-page="5"
        :sort-desc="sortDesc"
        :sort-by="sortBy"
        @click:sort="onSort"
      >
        <template v-slot:item="{ item }">
          <tr>
            <td><v-checkbox></v-checkbox></td>
            <td>{{ item.name }}</td>
            <td>{{ item.ac }}</td>
            <td>{{ item.hp }}</td>
            <td>{{ item.nowHp }}</td>
            <td>{{ item.cumulativeDamage }}</td>
            <td>
              <v-text-field
                v-model="item.damageInput"
                @keyup.enter="onDamageInputEnter(item)"
                hide-details="auto"
                class="custom-text-field"
              ></v-text-field>
            </td>
            <td>{{ item.initiative }} {{ item.initiative_cal }}</td>
          </tr>
        </template>

        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "BattleCmp",
  data() {
    return {
      lists: [],
      headers: [
        { text: "", value: "", sortable: true },
        {
          text: "",
          align: "start",
          value: "name",
          sortable: false,
        },
        { text: "AC", value: "ac" },
        { text: "HP", value: "hp" },
        { text: " HP", value: "nowHp" },
        { text: "", value: "cumulativeDamage" },
        { text: " ", value: "damageInput" },
        { text: "", value: "initiative", sortable: true },
      ],
      sortBy: "initiative", //   
      sortDesc: false, //    ()
      combat: false,
    };
  },
  computed: {
    ...mapGetters("monster", ["getMonsters"]),
    ...mapGetters("player", ["getPlayers"]),
  },
  created() {
    this.lists = this.getPlayers;
  },
  methods: {
    Onclick_battleStartBtn() {
      this.combat = !this.combat;
      //       
      //   initiative 1 20   
      this.lists.forEach((item) => {
        const rand = Math.floor(Math.random() * 20) + 1;

        item.initiative_tmp = item.initiative;

        item.initiative = Number(item.initiative) + rand;
        item.initiative_cal = " (" + item.initiative_tmp + " + " + rand + ")";

        item.nowHp = item.hp;
        item.cumulativeDamage = 0;
      });

      //       
      //    
      if (this.sortBy === "initiative") {
        //    'initiative' 
        this.sortBy = "name"; // 'name' 
      } else {
        //   
        this.sortBy = "initiative"; // 'initiative' 
      }
      this.sortDesc = !this.sortDesc; //   
    },
    onSort({ sortBy, sortDesc }) {
      //     
      this.sortBy = sortBy;
      this.sortDesc = sortDesc;
    },
    onDamageInputEnter(idx) {
      const input = idx.damageInput.trim(); //   

      if (input.startsWith("+")) {
        //  +   
        const damage = parseInt(input.substring(1)) || 0; // +     

        console.log("damage type : " + typeof damage);
        idx.nowHp = Math.min(idx.hp, idx.nowHp + damage); //  hp
        idx.cumulativeDamage = parseInt(idx.cumulativeDamage) - damage;
      } else {
        //    
        const damage = parseInt(input) || 0; //    0   

        console.log("damage type : " + typeof damage);

        idx.nowHp = Math.max(0, idx.nowHp - damage);
        idx.cumulativeDamage = parseInt(idx.cumulativeDamage) + damage;
      }

      console.log(idx.cumulativeDamage);
      idx.damageInput = ""; //   
    },
    Onclick_battleEndBtn() {
      this.combat = !this.combat;

      this.lists.forEach((item) => {
        item.initiative = item.initiative_tmp;
        item.initiative_cal = "";
      });
    },
  },
};
</script>

<style scoped>
.custom-text-field {
  width: 70px; /*    */
  border-bottom: none !important; /*   */
}
</style>
